<template>
  <div>
    <v-app-bar
      color="primary" dark fixed flat>
      <!-- <v-app-bar-nav-icon></v-app-bar-nav-icon> -->

      <v-toolbar-title class="ml-3 mr-5">Covid Tracker</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-text-field
        solo-inverted
        flat
        hide-details
        label="Search"
        prepend-inner-icon="mdi-magnify"
        v-model="searchString"
        v-on:keyup.enter="submit"
      />
      <v-spacer></v-spacer>
    </v-app-bar>
  </div>
</template>

<script>
// @ is an alias to /src
export default {
  name: "Navbar",
  data() {
    return{
      searchString: ''
    }
  },
  computed:{
    currentRouteName() {
        return this.$route.name;
    }
  },
  methods:{
    submit(){
      if(this.currentRouteName == "Country"){
        // this.$router.pop()
        this.$router.push({ name: 'Country', params: { id: this.searchString } });
      }
      this.$router.push({ name: 'Country', params: { id: this.searchString } });
      console.log(this.searchString);
      // this.searchString = '';


    }
  }
  

}
</script>
